<template>
    <div style="height: auto">
        <div>
            <el-button
                size="mini"
                icon="el-icon-back"
                @click="reverse"
            ></el-button
            ><el-button
                size="mini"
                icon="el-icon-right"
                @click="forward"
            ></el-button>
        </div>
        <iframe
            style="width: 100%; height: 100%"
            id="contentIframe"
            class="logframe"
            :src="baseApiUrl"
            frameborder="0"
        >
        </iframe>
    </div>
</template>

<script>
import { BaseApiUrl } from "@/api/api.js";
export default {
    data() {
        return {
            baseApiUrl: BaseApiUrl + /log/,
        };
    },
    mounted() {
        const oIframe = document.getElementById("contentIframe");
        const deviceWidth = document.documentElement.clientWidth;
        const deviceHeight = document.documentElement.clientHeight;
        oIframe.style.width = Number(deviceWidth) - 250 + "px"; //数字是页面布局宽度差值
        oIframe.style.height = Number(deviceHeight) - 160 + "px"; //数字是页面布局高度差
    },
    methods: {
        forward() {
            history.forward();
        },
        reverse() {
            history.back();
        },
    },
};
</script>

<style>
</style>


